.search {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100vw;
  height: 170px;
}

.search-form {
  width: 100vw;
  display: flex;
  align-items: center;
  justify-content: center;
}

.search .search-form .search-bar-main {
  padding: 0 30px;
  width: 50%;
  height: 55px;
  border-radius: 30px;
  border: none;
  background-color: #f5f6f7;
}
